import React, { Component } from 'react'

export default class Joke extends Component {
    state = {
        jokes:[], 
        loading:true
    }

    componentDidMount () {
        //fetch joke
        this.fetchJokeFromJokeAPI()
    }

    fetchJokeFromJokeAPI = () => {
        this.setState({ loading: true })
        const category = this.props.match.params.category
        const url = ` https://sv443.net/jokeapi/v2/joke/${category}?type=single&amount=10`

        fetch(url).then(response => response.json()).then(result => {
           this.setState({
               jokes: result.jokes,
               loading:false
           })
        }).catch(error => {
            console.log(error)
            this.setState({
                loading:false,
                message: "Ah, something went wrong"
            })
        })
    }

    render () {
        const { jokes, loading } = this.state
        return (
            <div style={{justifyContent:'center',
                         padding:50,
                         backgroundImage: "url('https://lh3.googleusercontent.com/proxy/VQB38CzPiZh20ZmEyQuDYcNeNTh_6dC2IX1o1xG7JGkXrWDdUEMCPawPXpu3LRSfIAfbeM0tJ2o5yi2MaD4HaJADE33-p1p9nRdJvr7WK-7YZr4z3w3z79q1dzA')",
                         backgroundSize: 'cover',
                         backgroundRepeat: 'no-repeat',
                         backgroundPosition: 'center',
                         color: '#800080',
                         minHeight: '100vh',
                         fontSize: 'larger',
                         Bottom: '3px'}}>
                <h3> {this.props.match.params.category} Jokes</h3>
                {loading && <p>Loading....</p>}
                {jokes.map((joke, idx)=> (
                <div key={idx}>
                    <p>{idx+1}. {joke.joke}</p>
                </div>
                ))}

                <button style={{borderRadius:'5px', backgroundColor: '#5b6db4', padding:'10px', border:'none'}} onClick={this.fetchJokeFromJokeAPI} >Get New Jokes</button>
            </div>
        )
    }
}